:root {
  --color-primary: #ef6eae;
  --color-success: #7effd4;
  --color-error: #ff1493;
  --color-info: #f8f8f8;
}

.app {
  background-color: #1f142d;
  min-height: 100vh;
  font-size: calc(10px + 2vmin);
  color: white;
  display: flex;
  flex-direction: column;
}

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 1rem;
}

.body {
  padding: 0 1rem 1rem;
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.container {
  width: min(500px, 100%);
}

.matrix {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 0;
  border: 1px solid var(--color-primary);
}

.cell {
  aspect-ratio: 1;
  font-size: 20px;
}

.cell {
  color: var(--color-primary);
  transition: 0.25s;
  background: none;
  border: 1px solid;
  line-height: 1;
}

.cell:disabled {
  background-color: lightgray;
}

.cell._hint {
  color: var(--color-success);
}

.cell._active {
  border-color: var(--color-info);
  color: var(--color-info);
}

.cell._success {
  animation: pulse 500ms;
  box-shadow: 0 0 0 8px transparent;
}

@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 var(--color-success);
  }
}

.toolbar {
  display: flex;
  margin-bottom: 1rem;
  justify-content: space-between;
  gap: 1rem;
  width: 100%;
}

.button {
  font-size: 1.5rem;
  padding: 0.5rem 1rem;
}

.title {
  margin-top: 0;
}

.score-count {
  position: absolute;
  right: 0;
  top: 0;
  font-size: 14px;
  opacity: 0;
}

.score-count._plus {
  color: var(--color-success);
}
.score-count._minus {
  color: var(--color-error);
}

.score-count._visible {
  animation: appear 500ms;
  animation-timing-function: ease-in-out;
}

@keyframes appear {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(-13px);
  }
}

.progress {
  width: 100%;
  margin-bottom: 1rem;
}

.progress .MuiLinearProgress-root {
  background-color: var(--color-info);
}
.progress .MuiLinearProgress-barColorPrimary {
  background-color: var(--color-primary);
}

.actions {
  display: grid;
  width: 100%;
  margin-top: 2rem;
  margin-bottom: 1rem;
}
